@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@300&500&display=swap');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Epilogue', sans-serif;
}

.create-team-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    width: 99.5vw;
    height: 100vh;
    font-size: 3vh;
}

.register-form {
    display: flex;
    flex-direction: column;
    width: 30vw;
}

.input-title {
    margin-bottom: .5vh;
    margin-top: 2.5vh;
}

.input-box {
    height: 3.5vh;
}

.button-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
}

.submit-button {
    font-size: 2.5vh;
    background: white;
    color: black;
    border-radius: 3vh;
    margin-top: 2.5vh;
    position: center;
    width: 20vw;
}

.submit-button:hover {
    color: white;
    background-color: #0984e3;
    cursor: pointer;
    transform: scale(1.15); 
    transition: ease-in-out .5s;
    animation: content_pulse 2s infinite;
}

p {
    text-align: center;
    font-size: 2vh;
}

.select-input {
    font-size: 2vh;
    height: 3.5vh;
    border-radius: 1.5vh;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}