@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
}

.page-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 99.5vw;
    height: 100vh;
}

.profile-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    width: 90vw;
    height: 100vh;
    font-size: 3vh;
}

.sidebar-container {
    display: inline-block;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    width: 10vw;
    height: 100vh;
    font-size: 3vh;
}

.sidebar-container button{
    width: 100%;
    margin: auto;
    height: 10vh;
    font-family: 'Audiowide';
}

.input-title {
    color: white;
    margin-bottom: .5vh;
    margin-top: 2.5vh;
    width: 20vw;
}

.input-box {
    height: 5vh;
    width: 20vw;
}

.submit-button {
    font-size: 2.5vh;
    background: white;
    color: black;
    border-radius: 3vh;
    margin-top: 2.5vh;
    position: center;
    width: 15vw;
}

.phone-input {
    font-size: 3vh;
    width: 20vw;
}

.register-form {
    display: inline-flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 90vw;
}

.select-input {
    font-size: 2vh;
    height: 3.5vh;
    border-radius: 1.5vh;
    width: 20vw;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}