@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@300&500&display=swap');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Epilogue', sans-serif;
